<template>
	<!--  -->
	<div class="abroad-block">
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
					<view-input :text="$GO_ABROAD[form.go_abroad]" width="310px"></view-input>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
          <view-input :text="$dateFmt(new Date(form.go_abroad_at), 'yyyy-MM-dd')" width="310px"></view-input>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
					<view-area-select :areaLs="areaLs" :type="2" item-width="153px"></view-area-select>
        </el-row>
			</el-main>
		</el-container>
	</div>
</template>

<script>
export default {
	data() {
		return {
			areaLs: [],
			form: {
				go_abroad: '', // 
				go_abroad_at: '', 
				go_abroad_country: '', 
				go_abroad_province: ''
			}
		}
	},
	methods: {
		initData(form) {
			this.$fillProps(this.form, form);
			this.areaLs = [+form.go_abroad_country, +form.go_abroad_province];
		}
	}
};
</script>

<style>
.detail-container {
	border: 1px #e3e3e3 solid;
	border-radius: 3px;
}
.detail-item {
	border-bottom: 1px #e3e3e3 solid
}
.detail-item:last-of-type {
	border-bottom: none
}
.detail-item-aside {
	background-color: #eee;    
	align-items: center;
	justify-content: center;
	display: flex;
}
.detail-item-main .item-row {
	margin-bottom: 5px
}
.detail-item-main .item-row:last-of-type {
	margin-bottom: 0
}
.detail-item-main .detail-item-width1 {
	width: 100px;
}
.detail-item-main .detail-item-width2 {
	width: 205px;
}
.detail-item-main .detail-item-width3 {
	width: 310px;
}
.abroad-block {
	border-bottom: 1px #e3e3e3 solid
}
</style>


